import { module } from './module';

class Controller {
    key: string = "devices";
    clientData: any;
    itemToolbars: Array<any> = [];

    constructor(private $scope, private fxAction, private toolbarUtils) {
        this.clientData = {
            rows: []
        };
        this.itemToolbars.push(
            this.toolbarUtils.btnBuilder("删除", "md-icon-button", false).iconBuilder("delete", {}).btnClick(($event, item) => {
                this.clientData.rows.remove(item);
            }).toValue()
        );
    }
}

Controller.$inject = ["$scope", "fxAction", "toolbarUtils"];

function Directive(): ng.IDirective {
    return {
        restrict: 'EA',
        template: require("./tpls/query.table.jade"),
        controller: Controller,
        controllerAs: 'qtCtl',
        link: function ($scope, $element, $attrs, $ctrl) {

        }
    };
}

module.directive('fxQueryTable', Directive);


